import React from "react";
import {reduxForm} from "redux-form";
import ItemInput from "../../../../../Commons/ItemInput/ItemInput";
import style from './ModalForm.module.scss';
import Item from "./Item/Item";


const ModalForm = ({handleSubmit}) => {

    let initialState = {
        items: [
            {title: 'A', active: true},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
            {title: '', active: false},
        ],
    };

    return (
        <form onSubmit={handleSubmit}>
            <div className={style.block_1}>
                <div className="form__series-wrap form__series-wrap-8">
                    <p className="form__series-title form__series-title-1 text-3
                    text-4_adaptive form__series-title-1_adaptive">
                           </p>
                    <div className={style.block_2}>
                        {initialState.items.map(i => <Item title={i.title} active={i.active}/>)}
                    </div>
                </div>
                <div className="form__series-wrap">
                    <div className="form__series">
                        <ItemInput name='admin-editing-modal-2-input-1'
                                   itemInputTitle=''
                                   input='search'/>
                    </div>
                </div>
            </div>
        </form>
    )
};

const ModalReduxForm = reduxForm({form: 'edit-admin-editing-modal-2-event'})(ModalForm);
export default ModalReduxForm;